$main-color: #54B689;
$main-text-color: rgb(5, 5, 5);
$border-color: #54B689;
$bio-bg-color: #f5f5f5;
$bio-border-color: #54B689;
$about-bg-color: #f5f5f5;
$black: #000;

@mixin mobile {
    @media (max-width: 425px) {
        @content;
    }
}

%title-text-shadow {
    text-shadow: 2px 1px #86cead;
}

%section-title {
    font-size: 3rem;
    margin-top: 2rem;
    text-align: center;
}

%center-bg {
    background-size: cover;
    background-position: center;
}

* {
    padding: 0;
    margin: 0;
    font-family: 'Roboto', sans-serif;
    box-sizing: border-box;
}

* {
    -webkit-user-select: none; /* Safari */
    -ms-user-select: none; /* IE 10 and IE 11 */
    user-select: none; /* Standard syntax */
}

* {
    scrollbar-width: thin;
    scrollbar-color: #54B689 white;
}

*::-webkit-scrollbar {
    width: 4px;
}

*::-webkit-scrollbar-track {
    background: white;
}

*::-webkit-scrollbar-thumb {
    background-color: #54B689;
}

a:link {
    text-decoration: none;
}

.highlight {
    color: $main-color;
}