@import '../../Global.scss';

#box-bio {
    display: flex;
    flex-direction: column;
    align-items: center;    
    padding-top: 2rem;

    h1 {
        color: $black;
    }
    
    .image-profile {
        width: 180px;
        height: 180px;
        margin: 10px;
        border-radius: 35px;
        border: 3px solid $main-color;
        padding: 0px;
    }

    #socials-container {
        margin: 5px;
        
        img {
            height: 25px;
            width: auto;
            margin-right: 10px;
            margin-top: 0px;
            vertical-align: top;
            padding: 0px;

            &:hover {
                height: 30px;
                width: auto;
                margin-right: 10px;
                margin-top: 0px;
                vertical-align: top;
            }
        }
    }

    #email-container {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin: 5px;
    }

    #qrcode {
        display: flex;
        align-items: center;
        flex-direction: column;
        margin-top: 20px;

        @include mobile {
            display: none;
        }

        img {
            width: 100px;
            height: 100px;
        }
    }
}
