@import '../../Global.scss';

#box-hard-skills {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    margin-top: 2rem;
    margin-bottom: 3rem;
    min-height: 40vh;
    

    h1 {
        text-align: center;
        color: $black;
    }

    p {
        text-align: left;
        color: $black;
        font-size: 1rem;
        border-left: 3px solid $main-color;
        padding-left: 0.5rem;
        margin: 0.2rem;
    }

    .list-items {
        display: flex;
        flex-direction: row;
        align-items: left;
        justify-content: left;
        align-items: left;
    }
    
    img {
        height: 30px;
        width: auto;
        margin: 5px;
        cursor: pointer;

        &:hover {
            height: 35px;
            width: auto;
        }
    }


}

.modal-skill {
    display: flex;
    flex-direction: row;
    align-items: left;
    justify-content: left;
    align-items: left;

    img {
        height: 30px;
        width: auto;
        margin-top: 5px;
        margin-right: 10px;
    }
}

.footer-modal-skill{
    display: flex;
    flex-direction: row;
    align-items: right;
    justify-content: right;
    align-items: right;
    margin-bottom: 0;
    margin-top: 2rem;
}
