@import '../../Global.scss';

#box-about {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;

    h1 {
        text-align: center;
        color: $black;
    }

    p {
        text-align: left;
        color: $black;
        font-size: 1.2rem;
        border-left: 3px solid $main-color;
        padding-left: 0.5rem;
        margin: 0.2rem;

        @include mobile {
            font-size: 1rem;
        }
    }
    
    #socials-container {
        margin: 5px;
        
        img {
            height: 25px;
            width: auto;
            margin-right: 10px;
            margin-top: 0px;
            vertical-align: top;
            padding: 0px;

            &:hover {
                height: 30px;
                width: auto;
                margin-right: 10px;
                margin-top: 0px;
                vertical-align: top;
            }
        }
    }

}
